import React, {useState,useEffect} from 'react'
import './Doublememory.css'
import dmimages from './dmimages'

function Doublememory() {
    const [choice1, setChoice1] = useState(false) 
    const [playerchoice, setPlayerchoice] = useState()
    const [menu, setMenu] = useState(true)
    const [playerpoints, setPlayerpoints] = useState(0)
    const [comppoints, setComppoints] = useState(0)
    const [result, setResult] = useState(null)
    const [instruct, setInstruct] = useState(false)
    
    const [carddif, setCarddif] = useState(false)
    const [cpudif, setCpudif] = useState(false)
    const [difl, setDifL] = useState(null)
    // const [difr, setDifR] = useState(null)
    const [compchoice, setCompchoice] = useState(null)
    const [turn, setTurn] = useState('player')
    const [retry, setRetry] = useState(false)
    const [comphasit, setComphasit] = useState([])

    
    let cards = []
    for(var clubs = 1; clubs<14;clubs++) cards.push(`hearts${clubs}`)
    for(var diamonds = 1; diamonds<14;diamonds++) cards.push(`hearts${diamonds}`)
    for(var spades = 1; spades<14;spades++) cards.push(`hearts${spades}`)
    for(var hearts = 1; hearts<14;hearts++) cards.push(`hearts${hearts}`)

    let arr1 = []
    let arr2 = []
    const exists = () => {
        var gridRow = Math.floor(Math.random()*11)
        var gridCol = Math.floor(Math.random()*11) 
        if(arr1.length === 0){
            arr1.push(gridRow)
            arr2.push(gridCol)
            return {gridRow, gridCol}
        }
        else{
            for(var i=0; i<arr1.length; i++){
                if(arr1[i]===gridRow && arr2[i]===gridCol){
                    return exists()
                }
                else{
                    if(i===arr1.length-1){
                        arr1.push(gridRow)
                        arr2.push(gridCol)
                        return {gridRow, gridCol}
                    }
                }
            }
    
        }
        // else return generateUniqueNumber30(arr)
    }

    const cardsPlacement = (e) => {
        setMenu(false)
        arr1 = []
        arr2 = []
        const container = document.querySelector('#doublememory__container')
        const childe = document.querySelectorAll(".card")

        // Removing elements and replacing
        if(childe){ childe.forEach(item=> item.parentNode.removeChild(item) ) }
        for(var i=0; i<(difl ? difl*26 : 104); i++){
            const card = document.createElement('span')
            card.classList.add('card', `card${i}`)
            container.appendChild(card)
            
            const middlesquare = document.createElement('span')
            middlesquare.classList.add('middlesquare')
            card.appendChild(middlesquare)

            for(var j=0; j<4;j++){
                const almonds = document.createElement('p')
                almonds.classList.add(`almonds${j}`,'alms')
                middlesquare.appendChild(almonds)
            }
            const line = document.createElement('p')
            const eye = document.createElement('span')
            eye.classList.add('eye')
            const p = document.createElement('p')
            const p2 = document.createElement('p')
            eye.appendChild(p)
            eye.appendChild(p2)
            line.classList.add('line')
            const circlet = document.createElement('p')
            circlet.classList.add('circlet')
            middlesquare.appendChild(line)            
            middlesquare.appendChild(eye)            
            middlesquare.appendChild(circlet)   
            const randX = Math.floor(100 * ((Math.random()*5)-5)/window.innerWidth)
            const randY = Math.floor(100 * ((Math.random()*5)-5)/window.innerHeight)
            const randRot = Math.floor(Math.random()*760)-360
            card.style.transform = `rotateZ(${randRot}deg)translate(${randX}vw,${randY}vh) scale(.6,.6)`
            const grids = exists()
            card.style.gridRow = grids.gridRow
            card.style.gridColumn = grids.gridCol

        }


        let fullcardshint = ".75vh -.75vh var(--c1), 1.5vh -1.5vh var(--c1), 2.25vh -2.25vh var(--c1), 3vh -3vh var(--c1), 3.75vh -3.75vh var(--c1), 4.5vh -4.5vh var(--c1),"+
        "0 .75vh var(--c1), .75vh .75vh var(--c1), .75vh 0 var(--c1), 1.5vh 0 var(--c1),"+
        "1.5vh -.75vh var(--c1), 2.25vh -.75vh var(--c1), 2.25vh -1.5vh var(--c1), 3vh -1.5vh var(--c1),"+
        "3vh -2.25vh var(--c1), 3.75vh -2.25vh var(--c1), 3.75vh -3vh var(--c1), 4.5vh -3vh var(--c1),"+
        "4.5vh -3.75vh var(--c1), 5.25vh -3.75vh var(--c1), 5.25vh -4.5vh var(--c1),"+
        ".75vh 1.5vh var(--c1), 1.5vh .75vh var(--c1), 2.25vh 0 var(--c1), 3vh -.75vh var(--c1),"+
        "3.75vh -1.5vh var(--c1), 4.5vh -2.25vh var(--c1), 5.25vh -3vh var(--c1), 6vh -3.75vh var(--c1) "
        let x = 0

        for(var assign=0; assign<(difl ? difl*26 : 104); assign++){
            const alms = document.querySelectorAll(`.card${assign}>.middlesquare>.alms`)
            let tolist = fullcardshint.split(',')

            // Suits changer
            x += 1; if(assign % 13===0){ x=0 }

            tolist[7+x] = tolist[7+x].replace('var(--c1)','#aa75aa')
            tolist = tolist.filter(e=> e!=="").join(',')
            // Setting box-shadow hint
            if(difl === 1){
                alms[0].style.boxShadow = tolist
            }
            if(difl === 2){
                if((assign >= 0 && assign <= 12) || (assign >= 26 && assign <= 38)) alms[0].style.boxShadow = tolist
                if((assign >= 13 && assign <= 25) || (assign >= 39 && assign <= 51)) alms[1].style.boxShadow = tolist
            }
            if(difl === 3){
                if((assign >= 0 && assign <= 12) || (assign >= 39 && assign <= 51)) alms[0].style.boxShadow = tolist
                if((assign >= 13 && assign <= 25) || (assign >= 52 && assign <= 64)) alms[1].style.boxShadow = tolist
                if((assign >= 26 && assign <= 38) || (assign >= 65 && assign <= 77)) alms[3].style.boxShadow = tolist
            }
            if(difl === 4 || !difl){
                if((assign >= 0 && assign <= 12) || (assign >= 52 && assign <= 64)) alms[0].style.boxShadow = tolist
                if((assign >= 13 && assign <= 25) || (assign >= 65 && assign <= 77)) alms[1].style.boxShadow = tolist
                if((assign >= 26 && assign <= 38) || (assign >= 78 && assign <= 90)) alms[2].style.boxShadow = tolist
                if((assign >= 39 && assign <= 51) || (assign >= 91 && assign <= 103)) alms[3].style.boxShadow = tolist
            }
        }
    }

    const imageLocator = (num,i) => {
        switch(num){
            case 1: return i===0 ? dmimages.clubs1 : i===1 ? dmimages.diamond1 : i===2 ? dmimages.hearts1 : dmimages.spade1; 
            case 2: return i===0 ? dmimages.clubs2 : i===1 ? dmimages.diamond2 : i===2 ? dmimages.hearts2 : dmimages.spade2; 
            case 3: return i===0 ? dmimages.clubs3 : i===1 ? dmimages.diamond3 : i===2 ? dmimages.hearts3 : dmimages.spade3; 
            case 4: return i===0 ? dmimages.clubs4 : i===1 ? dmimages.diamond4 : i===2 ? dmimages.hearts4 : dmimages.spade4; 
            case 5: return i===0 ? dmimages.clubs5 : i===1 ? dmimages.diamond5 : i===2 ? dmimages.hearts5 : dmimages.spade5; 
            case 6: return i===0 ? dmimages.clubs6 : i===1 ? dmimages.diamond6 : i===2 ? dmimages.hearts6 : dmimages.spade6; 
            case 7: return i===0 ? dmimages.clubs7 : i===1 ? dmimages.diamond7 : i===2 ? dmimages.hearts7 : dmimages.spade7; 
            case 8: return i===0 ? dmimages.clubs8 : i===1 ? dmimages.diamond8 : i===2 ? dmimages.hearts8 : dmimages.spade8; 
            case 9: return i===0 ? dmimages.clubs9 : i===1 ? dmimages.diamond9 : i===2 ? dmimages.hearts9 : dmimages.spade9; 
            case 10: return i===0 ? dmimages.clubs10 : i===1 ? dmimages.diamond10 : i===2 ? dmimages.hearts10 : dmimages.spade10; 
            case 11: return i===0 ? dmimages.clubs11 : i===1 ? dmimages.diamond11 : i===2 ? dmimages.hearts11 : dmimages.spade11; 
            case 12: return i===0 ? dmimages.clubs12 : i===1 ? dmimages.diamond12 : i===2 ? dmimages.hearts12 : dmimages.spade12; 
            case 13: return i===0 ? dmimages.clubs13 : i===1 ? dmimages.diamond13 : i===2 ? dmimages.hearts13 : dmimages.spade13; 
            default: return;
        }
    }


    useEffect(()=>{
        if(turn === 'cpu'){
            computerMovement()
        }
    },[turn])

    useEffect(()=>{
        if(result !== null){
            const cards = document.querySelectorAll('.card')
            const container = document.querySelector('#doublememory__container')
            cards.forEach(card=>{
                // card.style.opacity = '0'
                const midsquare= card.querySelector('.middlesquare')
                card.classList.add('flipped')
                
                setTimeout(()=>{
                    midsquare.style.opacity = 0
                    const almsfinder = midsquare.querySelectorAll('.alms')
                    almsfinder.forEach((alm,i) => {
                        if(alm.style.boxShadow.search('#')>0){
                            const colorfinder = alm.style.boxShadow.split(',')
                            colorfinder.forEach(color => {
                                if(color.includes("#")){
                                    var num = colorfinder.indexOf(color) - 6;
                                    const image_locator = imageLocator(num,i)
                                    card.style.backgroundImage = `url(${image_locator})`
                                }
                            })
                        }
                    })
                    midsquare.opacity = 0;
                    setTimeout(()=>{
                        container.style.pointerEvents = 'fill'
                    },250)
                },250)

            })
        }

    },[result])


    const eventLocator = (e) => {
        if(e.target.classList[0]==='card' && e.target.classList[1].search(/[0-9]/g)){
            // player's first card choice
            if(choice1 === false){
                setComphasit([])

                setChoice1(true)
                e.target.classList.add("flipped")
                const squarefinder = e.target.querySelector(".middlesquare") 
                const almsfinder = squarefinder.querySelectorAll(".alms")

                setTimeout(()=>{
                    almsfinder.forEach((item,i)=>{
                        if(item.style.boxShadow.search("#")>0){
                            const colorfinder = item.style.boxShadow.split(',')
                            colorfinder.forEach(item2 => {
                                if(item2.includes("#")){
                                    var num = colorfinder.indexOf(item2) - 6;
                                    // Locating Image
                                    const image_locator = imageLocator(num,i)
                                    setPlayerchoice(image_locator)
                                    e.target.style.backgroundImage = `url(${image_locator})`
                                }
                            })
                        }
                    })
                    e.target.querySelector('.middlesquare').style.opacity = 0;
                },250)
            }

        // player's second card choice: check similarities
            else{
                setChoice1(false)
                setPlayerchoice(null)
                e.target.style.zIndex = 3;

                const container = document.querySelector('#doublememory__container')
                const cards = document.querySelectorAll('.card')
                container.style.pointerEvents = "none"
                e.target.classList.add("flipped")
                const squarefinder = e.target.querySelector(".middlesquare") 
                const almsfinder = squarefinder.querySelectorAll(".alms")
                // e.target.style.transform = `rotateY(180deg) scale(.6,.6) `
                // let imageGet;
                setTimeout(()=>{
                    almsfinder.forEach((item,i)=>{
                        if(item.style.boxShadow.search("#")>0){
                            const colorfinder = item.style.boxShadow.split(',')
                        
                            colorfinder.forEach(item2 => {
                                if(item2.includes("#")){
                                    var num = colorfinder.indexOf(item2) - 6;
                                    const image_locator = imageLocator(num,i)

                                    const flipped = document.querySelectorAll(".flipped")
                                    e.target.style.backgroundImage = `url(${image_locator})`
                                    // If player gets wrong answer.
                                    if(playerchoice !== image_locator){
                                        setTimeout(()=>{
                                        
                                            flipped.forEach(item=>{
                                                setTimeout(()=>{
                                                    item.style.zIndex = "1"
                                                    item.style.backgroundImage = "none"
                                                    item.querySelector(".middlesquare").style.opacity = 1;
                                                    // setPlayerturn(false)
                                                    setTurn('cpu')
                                                },250)
                                                item.classList.remove("flipped")
                                            })
                                        },750)
                                    }
                                    // If player gets right answer.
                                    else{
                                        setPlayerpoints(playerpoints => playerpoints + 1)
                                        if((playerpoints+1)*2 > cards.length){
                                            setResult('win')
                                        }
                                        else{
                                            setTimeout(()=>{
                                                flipped.forEach(item=>{
                                                    // item.classList.remove(item.classList[1])
                                                    item.classList.remove("flipped")
                                                  
                                                    setTimeout(()=>{
                                                        item.parentNode.removeChild(item) 
                                                        container.style.pointerEvents = "fill"

                                                    },250)
                                                })
                                            },750)
                                        }
                                        
                                    }
                                }
                            })
                        }
                    })
                    e.target.querySelector('.middlesquare').style.opacity = 0;
                },250)
            }
        }
    }

    useEffect(()=>{
        if(compchoice !== null){
            computerMovement()
        }
    },[compchoice])



    useEffect(()=>{
        if (retry === true)
        computerMovement()

    },[retry])


    function generateUniqueNumber(limit) {
        var randomselector = Math.floor(Math.random() * limit)
        if (comphasit.includes(randomselector)) 
            return generateUniqueNumber(limit)
        else {
            setComphasit([...comphasit, randomselector]);
            return randomselector
        }
    }

    const computerMovement = () => {
        setRetry(false)
        // PROBLEM: randChoice picks the same card chosen.
        const cards = document.querySelectorAll(".card")
        var randChoice = generateUniqueNumber(cards.length)
        // var randChoice = Math.floor(Math.random()*cards.length)
        const container = document.querySelector('#doublememory__container')
        const midsquare = cards[randChoice].querySelector(".middlesquare")
        const almsfinder = midsquare.querySelectorAll(".alms")
        
        // Delays computer movement 
        setTimeout(()=>{
            cards[randChoice].classList.add("flipped")
            // Before card completely flips, change background and remove middlesquare
            setTimeout(()=>{
                // Locate row of stars that include the hint
                almsfinder.forEach((item, i)=>{
                    if(item.style.boxShadow.search('#')>0){
                        const colorfinder = item.style.boxShadow.split(',')
                        colorfinder.forEach(item2 => {
                            if(item2.includes('#')){  
                                var num = colorfinder.indexOf(item2)-6
                                const image_locator = imageLocator(num,i)
                                cards[randChoice].style.backgroundImage = `url(${image_locator})`
                                // remove middle square
                                setTimeout(()=>{
                                    midsquare.style.opacity = '0'
                                })
                                // enter useEffect
                                if(compchoice === null){
                                    setCompchoice(image_locator)
                                }
                                // useEffect's effect
                                else{
                                    setComphasit([])
                                    const flipped = document.querySelectorAll('.flipped')
                                    var cover = 1500
                                    // if computer gets wrong. Player's turn
                                    if(compchoice !== image_locator){
                                        setCompchoice(null)
                                        setTurn('player')
                                        setTimeout(()=>{
                                        flipped.forEach(item=>{
                                            setTimeout(()=>{
                                                item.style.zIndex = "1"
                                                item.style.backgroundImage = "none"
                                                item.querySelector(".middlesquare").style.opacity = 1;
                                                container.style.pointerEvents = "fill"
                                            },250)
                                            item.classList.remove("flipped")
                                            
                                        })
                                        },cover)
                                    }
                                    // if computer gets right. Computer reselects
                                    else{
                                        setCompchoice(null)
                                        setComppoints(comppoints => comppoints + 1)
                                        if((comppoints+1)*2 > cards.length){
                                            setResult('lose')
                                        }
                                        else{
                                            setTimeout(()=>{
                                                // if(comppoints )
                                                flipped.forEach(item=>{
                                                    // item.classList.remove(item.classList[1])
                                                    item.classList.remove("flipped")
                                                    setTimeout(()=>{
                                                        item.parentNode.removeChild(item) 
                                                        setTimeout(()=>{
                                                            setRetry(true)
                                                        },250)
                                                    },250)
                                                })
                                            },cover)
                                        }
                                    }
                                    
                                }

                            }
                        })
                        // indexOf('#aa75aa')
                    }
                })

            },250)
        },250)
         
        
    }


    const resetHandlre = () => {
        const childe = document.querySelectorAll(".card")
        childe.forEach(item=> item.parentNode.removeChild(item) )
        setChoice1(false)
        setPlayerchoice(null)
        setMenu(true)
        setPlayerpoints(0)
        setComppoints(0)
        setResult(null)
        setCompchoice(null)
        setTurn('player')
        setRetry(false)
        setComphasit([])
    }

    
    const cardDifficulty = (dif) => {
        setDifL(dif)
        
    }
    const cpuDifficulty = (dif) => {
        // setDifR(dif)
    }

    return (
        <div onMouseUp={eventLocator} id='doublememory__container'>
            <div className={`menu__container ${menu ? '' : 'gamestart'}`}>
                <h1>DOUBLE MEMORY</h1>
                <button onMouseUp={cardsPlacement}>play</button>
                <div onMouseUp={()=>setInstruct(!instruct)} className="instruct__button">?</div>
                <div className="difficulty">
                    <div className="difficulty__card">
                        <ul onMouseUp={()=>setCarddif(!carddif)} style={{height: carddif ? "180px": "20px"}}>{difl ? difl+' Suit' : 'Card Difficulty'}
                            <li onMouseUp={()=>cardDifficulty(1)}>1 Suit</li>
                            <li onMouseUp={()=>cardDifficulty(2)}>2 Suit</li>
                            <li onMouseUp={()=>cardDifficulty(3)}>3 Suit</li>
                            <li onMouseUp={()=>cardDifficulty(4)}>4 Suit</li>
                        </ul>
                    </div>
                    <div className="difficulty__cpu">
                        <ul onMouseUp={()=>setCpudif(!cpudif)} style={{height: cpudif ? "180px": "20px"}}>CPU Difficulty
                            <li onMouseUp={()=>cpuDifficulty('gold')}>Goldfish</li>
                            <li onMouseUp={()=>cpuDifficulty('man')}>???</li>
                            <li onMouseUp={()=>cpuDifficulty('al')}>???</li>
                            <li onMouseUp={()=>cpuDifficulty('omni')}>???</li>
                        </ul>
                    </div>
                </div>
                {instruct ? 
                    <div className="instruction__container"  onMouseUp={()=>setInstruct(!instruct)} >
                        
                        <div className="instructions">
                            <p>
                                <span style={{ textAlign: 'center', fontSize: 30, marginBottom: 10 }}>Directive:</span>
                                26 to 104 cards, respective of the card's difficulty, will be set upon the table. 4 suits of 12 that contains either Clubs, Diamonds, Hearts, or Spades, and their duplicate.
                                First turn goes to the Player who has to pick 2 cards of the ones layed before them. If the 2 chosen cards is similar, the Player recieves a point and picks another 2 cards. If the Player fails to pick 2 similar cards, the turn goes to the CPU and vice-versa.
                                <br/>Try and get higher points than your opponent!<br/><br/>
                                <span style={{textAlign: 'center'}}>NOTICE:<br/>This is still a work in progress...</span>
                            </p>
                        </div>
                
                    </div> : ""}

            </div> 

            { !menu ? 
                <div className={`scoreboard ${result? 'scoreveil' : ''}`}>
                    <span className="playerscore">{playerpoints}</span>
                    <span>-</span>
                    <span className="compscore">{comppoints}</span>
                </div> : ""
            }
            { !menu ?
                <button onMouseUp={resetHandlre} className="reset__btn">reset</button> : ""
            }
            
        </div>
    )
}

export default Doublememory
